    export default {
        data() {
            return {
                value: 1,    // 默认选中昨天
                startTime: null,  //  自定义时间
                dataTime: null,   //  筛选时间
                show: false,   // 筛选时间的弹窗显示隐藏
                nameList: [],
            }
        },
        created(){
            this.dataTime = this.common.commonFun.get_date(1)
            this.getInfo();
        },
        methods:{
            getInfo(){
                this.nameList = [];
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.serviceRanking,
                    type: "post",
                    data: {
                        date_time: this.dataTime,
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response);
                        this.nameList = response;
                    }
                });
            },
            // 顶部筛选时间
            date(index){
                if(index == 0){
                    this.dataTime = this.common.commonFun.get_date(0);
                    this.getInfo();
                }
                if(index == 1){
                    this.dataTime = this.common.commonFun.get_date(1);
                    this.getInfo();
                }
                if(index == 2){
                    this.show = !this.show;
                }
            },
            //   筛选时间弹窗确定按钮
            onConfirm(){
                this.dataTime = this.startTime;
                this.getInfo();
            },
            //  我要分享了
            navRigth(){
                console.log("我可能要分享了")
            },
//            排序变化
            order(){
                this.nameList.reverse()
            },
            orderEye(item){
               this.$router.push({
                   name: 'serveTime',
                   query: {
                       id: item.user_id
                   },
                   params:{
                       time: this.value1,
                       value: 2
                   }
               })
            }
        }
    }



